@import url(https://fonts.googleapis.com/css2? família=Roboto:wght@400;700&display=swap);
* {
    box-sizing: border-box;
}

body {
    background-color: #111;
    color: #fff;
    margin: 0;
    font-family: 'Roboto', sans-serif;
}

.app--list {
    margin-top: 0;
}

.loading {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 99;
    background-color: #000;
    display: flex;
    justify-content: center;
}
.MovieRow {
    margin-bottom: 40px;
}

.MovieRow h2 {
    margin: 0px 0px 0px 30px;
}

.movieRow--area {
    overflow-x: hidden;
    padding-left: 30px;
}

.movieRow--item {
    display: inline-block;
    width: 150px;
    cursor: pointer;
}

.movieRow--item img {
    width: 100%;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    transition: all ease 0.2s;
}

.movieRow--list {
    transition: all ease 0.6s;
}

.movieRow--item img:hover {
    -webkit-transform: scale(1);
            transform: scale(1);
}

.movierow--left,
.movierow--right {
    position: absolute;
    width: 30px;
    height: 225px;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    opacity: 0;
    transition: all ease 0.6;
}

.movierow--right {
    right: 0;
}
.movierow--left {
    left: 0;
}

.MovieRow:hover .movierow--left,
.MovieRow:hover .movierow--right {
    opacity: 1;
}
@media (max-width:760px) {
    .movierow--left,
    .movierow--right {
        opacity: 1;
    }
}
.featured {
    height: 70vh;
}

.featured--vertical {
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);
}

.featured--horizontal {
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 30px;
    /* padding-bottom: 150px; */
    padding-top: 70px;
}

.featured--name {
    font-size: 60px;
    font-weight: bold;
    margin-right: 15px;
}

.featured--info {
    font-size: 18px;
    margin-top: 15px;
    margin-right: 15px;
}

.featured--points, 
.featured--year, 
.featured--seasons {
    display: inline-block;
    margin-right: 15px;
    font-weight: bold;
}

.featured--points {
    color: #46d369; 
}

.featured--description {
    margin-top: 15px;
    font-size: 20px;
    color: #999;
    max-width: 40%;
}

.featured--buttons {
    margin-top: 15px;
}

.featured--watchbutton, 
.featured--mylistbutton {
    display: inline-block;
    font-size: 20px;
    font-weight: bold;
    padding:15px 25px;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;
    opacity: 1;
    transition: all ease 0.2s;
}

.featured--watchbutton:hover, 
.featured--mylistbutton:hover {
    opacity: 0.7;
}

.featured--watchbutton {
    background-color: #fff;
    color: #111;
}
.featured--mylistbutton {
    background-color: #333;
    color: #fff;
}

.featured--genres {
    margin-top: 15px;
    font-size: 18px;
    color: #999;
}

@media (max-width:760px) {
    .featured {
        height: 80vh;
    }

    .featured--name {
        font-size: 40px;
    }

    .featured--info {
        font-size: 16px;
    }

    .featured--description {
        font-size: 14px;
        max-width: 100%;
    }

    .featured--watchbutton, 
    .featured--mylistbutton {
        font-size: 16px;
   }

   .featured--genres {
    font-size: 15px;
   }
}
header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 70px;
    z-index: 999;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 30px;
    padding-right: 30px;
    background: transparent;
    transition: all ease 0.5s;
}

header.black {
 background-color: #141414;
}

.header--logo img {
    height: 30px;
}

.header--logo {
    height: 30px;
}

.header--user img {
    height: 40px;
    border-radius: 4px;
}

.header--user {
    height: 40px;
}

